<template>
  <div class="card">
    <img class="card-img-top" v-bind:src="iconsrc" alt="Folder icon" />
    <div class="card-body">
      <h5 class="card-title">File List</h5>
      <p class="card-text">Your files list.</p>
      <a href="javascript:void(0);" class="btn btn-primary" v-on:click="gotofilelist">View your files</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FileListCard',
  data () {
    return {
      iconsrc: require('@/assets/folder.png'),
    }
  },
  methods: {
    gotofilelist: function () {
      this.$router.push({path: '/file/list'})
    },
  },
}
</script>



