<template>
  <div class="card">
    <img class="card-img-top" v-bind:src="iconsrc" alt="Account icon" />
    <div class="card-body">
      <h5 class="card-title">Account</h5>
      <p class="card-text">Your wallet account.</p>
      <a href="javascript:void(0)" class="btn btn-primary" v-on:click="gotoaccount">View your account</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AccountCard',
  data () {
    return {
      iconsrc: require('@/assets/wallet.png'),
    }
  },
  methods: {
    gotoaccount () {
      this.$router.push({path: '/account'})
    }
  },
}
</script>



