<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 text-left">
        <h3>
          <span v-on:click="goback">
            <span class="glyphicon glyphicon-chevron-left float-left"></span>Go back</span>
        </h3>
      </div>
    </div>
    <div class="row">
      <table class="table stats-table">
        <thead>
          <tr>
            <th>Address</th>
            <th>Currency</th>
            <th>Amount</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row">{{ account.address }}</th>
            <td>{{ account.currency }}</td>
            <td><h5>{{ account.amount }} <i class="fa fa-money"></i></h5></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Account',
  data () {
    return {
      account: {},
    }
  },
  created () {
    // request for user's account information here
    this.account = require('./account.js').account
  },
  methods: {
    goback: function () {
      this.$router.back()
    },
  },
}
</script>



