<template>
  <div class="card">
    <img class="card-img-top" v-bind:src="iconsrc" alt="User avatar" />
    <div class="card-body">
      <h5 class="card-title">Tom</h5>
      <p class="card-text">User description.</p>
      <a href="#" class="btn btn-primary">Click to edit profile</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserInfo',
  data () {
    return {
      iconsrc: require('@/assets/avatar.png'),
    }
  },
}
</script>



