<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 text-left">
        <h3>
          <span v-on:click="goback">
            <span class="glyphicon glyphicon-chevron-left float-left"></span>Go back</span>
        </h3>
      </div>
    </div>
    <div class="row">
      <div class="col-12">
        <h1>{{ archive.title }}</h1>
      </div>
      <div class="col-sm-8">
        <p>{{ archive.abstract }}</p>
      </div>
      <div class="col-sm-4">
        <img v-bind:src="archive.imgsrc" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FilePreview',
  data () {
    return {
      archive: {},
    }
  },
  created () {
    // request for archive detail
    var archives = require('../FileList/files.js').archives
    for (var i = 0; i < archives.length; i++) {
      if (this.$route.params.fileId === archives[i].fileId) {
        this.archive = archives[i]
        break
      }
    }
  },
  methods: {
    goback: function () {
      this.$router.back()
    },
  },
}
</script>



