<template>
  <div class="card">
    <img class="card-img-top" v-bind:src="iconsrc" alt="Upload icon" />
    <div class="card-body">
      <h5 class="card-title">Upload files</h5>
      <p class="card-text">Upload Interlectual property.</p>
      <a href="javascript:void(0)" class="btn btn-primary" v-on:click="gotoupload">Upload your IP</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UploadCard',
  data () {
    return {
      iconsrc: require('@/assets/upload.png'),
    }
  },
  methods: {
    gotoupload () {
      this.$router.push({path: '/uploadfile'})
    },
  },
}
</script>



