<template>
  <div class="container">
    <div class="row">
      <h1 class="col-sm-12">You have successfully logout.</h1>
    </div>
    <div class="row">
      <h3 class="col-sm-12">{{ msg }}</h3>
    </div>
    <div class="row">
      <span class="col-sm-12">
      <button class="btn btn-primary" v-on:click="gotologin">Go to login page</button>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LogoutPage',
  data: function () {
    return {
      msg: 'You have successfully logout.',
    }
  },
  methods: {
    gotologin: function () {
      var msg = 'You have successfully logout.'
      this.$router.push({path: '/login/' + msg})
    },
  },
}
</script>



