<template>
  <div class="card">
    <img class="card-img-top" v-bind:src="iconsrc" alt="Search icon" />
    <div class="card-body">
      <h5 class="card-title">Search files</h5>
      <p class="card-text">Search Interlectual property.</p>
      <a href="#" class="btn btn-primary">Search IP</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchCard',
  data () {
    return {
      iconsrc: require('@/assets/search.png'),
    }
  },
}
</script>



